import {getters} from './getters'
import {actions} from './actions'
import {mutations} from './mutations'

// initial state
const state = {
  contactPersonList: [], // 
  contactPersonCount: -1, // 
  contactCompanyList: [], // 
  contactCompanyCount: -1, // 
  filterType: -1,
  contactType: 0,        // 
  loadingTable: false, // table
  basicLoading: false,  // 
  cmpBcLoading: false, // 
  descLoading: false, // 
  tagLoading: false, // 
  recordLoading: false, // 

  contactId: '', // /Id
  detailType: '', // 
  desc: {}, // 
  personBasicInfo: [], // 
  companyBasicInfo: [], // 
  contactTags: [], // 
  tags: [], // 
  contactDetail: {
    base: {},
    social: [],
    contact: [],
    stage: [],
    system: []
  }, // 
  timeLine: [], // 
  /**
   *  
   * @type {Object}
   */
  timeLinePage: {
    totalPages: -1,
    page: -1
  }, // 
  editorPerson: {}, // 
  editorCompany: {}, // 
  companyDetail: {
    base: {},
    social: [],
    contact: [],
    stage: [],
    system: []
  }, // 

  allParams: [], // 
  isAdded: false, // 

  showMsgBtn: false, // 
  filterText: '', // 

  isEditOver: true //   
}

export default {
  state,
  getters,
  actions,
  mutations
}



